<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
        @updateHandle="updateHandle"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
      <div class="card">
        <div class="card-header">
          <h6 class="title">Danh sch bi vit</h6>
        </div>
        <div class="card-body form-card">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="articleInBlog" :actions="actions" :actionsTable="[]"/>
        </div>
      </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao din</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao din"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hy</el-button>
        <el-button type="primary" @click="save">Cp nht</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Message, MessageBox, Input, Button } from 'element-ui';
import MySelect from 'src/components/UIComponents/Select';
import MyTable from 'src/components/UIComponents/Table.vue'
import dataFrom from './blog-form'
import { mapState } from 'vuex'
import util from '@/helpers/util'
import dtHelper from 'src/helpers/datatable'
import articleSchemas from './article-blog-schemas'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    Message,
    MySelect,
    CustomField,
    MyTable
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    if (this.$store.state.customFieldDefine.blog){
      _custom_field = this.$store.state.customFieldDefine.blog.reduce(function (result, item) {
        result[item.handle] = '';
        return result;
      }, {});
    }

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    let id = this.$route.params.id;
    this.$store.dispatch('getCustomField', {
      type: 'blog',
      id: id
    });
    this.$store.dispatch('getListThemeView', 'blog');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      custom_field: _custom_field,
      show_fields: [],
      actions: [
        {
          type: 'danger',
          icon: 'nc-icon nc-simple-remove',
          callback: this.removeArticle
        },
      ],
      columnDefs: dtHelper.buildColumDefs(articleSchemas),
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.blogDetail,
      articleInBlog: state => state.articleInBlog,
    }),
    form: {
      get () {
        let hashSeo = {};
        if (this.originalForm.seo){
          hashSeo = util.hashFlatten({seo: this.originalForm.seo});
        }
        let _originalForm = Object.assign({}, this.originalForm, hashSeo)
        return _originalForm;
      },
      set (value) {}
    },
    customField: {
      get() {
        let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
        for (let item of data) {
          if (item.value) {
            _custom_field[item.handle] = item.value;
          } else {
            _custom_field[item.handle] = '';
          }
        }
        return data||'';
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mc nh',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },
  mounted() {
    this.getContext();
    let id = this.$route.params.id;
    this.$store.dispatch('fetchBlogDetail', id);

    this.$store.dispatch('setPageTitle', 'Cp nht nhm bi vit');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Xem trn web',
      type: 'default',
      icon: '',
      callback: this.view
    },{
      label: 'Xa',
      type: 'warning',
      icon: '',
      callback: this.remove
    }, {
      label: 'Cp nht',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },
  methods: {
    removeArticle(index, row){
      let self = this;
      MessageBox.confirm('Bn c chc chn xa khi nhm bi vit khng?', 'Warning', {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy b',
        type: 'warning',
        center: true
      }).then(() => {
        this.$store.dispatch('removeArticleToBlog', {
          arrIdBlog: [self.form.id],
          arrId: [row.id]
        }).then(res => {
          Message({
            type: 'success',
            message: ' xa bi vit khi nhm bi vit'
          });
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hy b'
        });
      })
    },
    changeTreeArticle(node, targetTree, oldTree){
      let self = this
      let articles = [];
      let data = targetTree.data;
      data.forEach(function (element, index) {
        articles.push(element.id);
      })
      this.$store.dispatch('updatePriorityArticle', {
        listArticle: articles,
        blogID: self.form.id
      }).then(result => {
        Message({
          type: 'success',
          message: 'Cp nht thnh cng'
        });
      }).catch(error => {
        Message({
          type: 'warning',
          message: 'C li xy ra'
        });
      })
    },
    cancel() {
      this.$router.push({name: 'AllBlogs'});
    },
    updateHandle(){
      let self = this;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.$store.state.blogDetail = self.form;
        self.$store.state.blogDetail.handle = result;
      });
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bn c chc chn xa nhm bi vit khng?', 'Warning', {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy b',
        type: 'warning',
        center: true
      }).then(() => {
        self.$store.dispatch('removeBlog', self.form.id).then(res => {
          Message({
            type: 'success',
            message: ' xa nhm bi vit'
          });
          self.$router.push({name: 'AllBlogs'});
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hy b'
        });
      })
    },
    view(){
      let self = this;
      var win = window.open(self.form.url, '_blank');
      if (win) {
        win.focus();
      } else {
        Message({
          message: 'Please allow popups for this website',
          type: 'danger'
        });
      }
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          if (self.form.parent) {
            self.form.parent_id = self.form.parent.id;
          }
          self.$store.dispatch('updateBlog', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'blog', self.form.id));

            Message({
              message: 'Cp nht thnh cng',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      })
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'blogs'});
      this.$store.state.ctx = ctx;
      return ctx;
    },
  },
  watch: {
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.blogDetail = [];
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
